import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import PaymentSuccessful from '../resources/images/Payment_success.png';
import Hidden from '@material-ui/core/Hidden';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import SecurePay from '../resources/images/Secure_payment.png';
import '../style.css';

const useStyles = makeStyles((theme) => ({
    pay: {
        margin: 'auto',
        [theme.breakpoints.down("xs")]: {    
            position: 'relative',
            top: '5%',
            transform: 'translateY(5%)',
            height: '50vmin',
            width: '70vmin', 
        },
        [theme.breakpoints.up("sm")]: {
            position: 'relative',
            top: '5%',
            transform: 'translateY(5%)',
            height: '60vmin',
            width: '80vmin'
        },
        [theme.breakpoints.between('750', '1000')]: {
            position: 'relative',
            top: '5%',
            transform: 'translateY(5%)',
            height: '48vmin',
            width: '68vmin',
        },
        [theme.breakpoints.up("md")]: {
            position: 'relative',
            top: '5%',
            transform: 'translateY(5%)',
            height: '45vmin',
            width: '56vmin',
        },
        [theme.breakpoints.up("lg")]: {
            position: 'relative',
            top: '5%',
            transform: 'translateY(5%)',
            height: '52vmin',
            width: '72vmin',       
        },
        [theme.breakpoints.up("xl")]: {
            position: 'relative',
            top: '5%',
            transform: 'translateY(5%)',
            height: '54vmin',
            width: '74vmin',
        },
    },
    SQP: {
        [theme.breakpoints.up("md")]: {
            height: '12vmin',
            width: '16vmin',
        },
        [theme.breakpoints.up("lg")]: {
            height: '12vmin',
            width: '16vmin',       
        },
        [theme.breakpoints.up("xl")]: {
            height: '12vmin',
            width: '16vmin',
        },
    },
    text: {
        margin: 'auto',
        [theme.breakpoints.up("md")]: {
            marginTop: '80px'
        },
        [theme.breakpoints.up("lg")]: {
            marginTop: '20px'       
        },
        [theme.breakpoints.up("xl")]: {
            marginTop: '10px'
        },
    },
}));


export default function Notification() {

    const classes = useStyles();

    const redirect = () => {
        setTimeout(function () {
            window.location.href = "/";
        }, 5000);
    };

    useEffect(() => {
        redirect();
    });

    return (
        <div>  
            <Dialog
                open={true}
                aria-labelledby="responsive-dialog-title"
                className="main">
                
                <Hidden only={['xs', 'sm']}>
                    <DialogTitle id="responsive-dialog-title">
                        <img src={SecurePay} className={classes.SQP} alt="Secure Pay" />
                    </DialogTitle>
                </Hidden>    
                <DialogContent>
                    <DialogContentText>
                        <div  style={{textAlign: 'center'}}>
                            <img className={classes.pay} src={PaymentSuccessful} alt="Payment Successfull" />
                        </div>
                        <Hidden only={['xs', 'sm']}>
                            <div className={classes.text} style={{textAlign: 'center'}}>
                                <p style={{textAlign: 'center', fontSize: '28px', fontWeight: 'bold'}}>Thank You !!! </p>
                            </div>  
                        </Hidden>
                    </DialogContentText>
                </DialogContent>
            </Dialog>
        </div>
    );
}

