import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Link from '@material-ui/core/Link';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Divider from '@material-ui/core/Divider';
import GitHubIcon from '@material-ui/icons/GitHub';
import LinkedInIcon from '@material-ui/icons/LinkedIn';

const useStyles = makeStyles(theme => ({
    root: {
        backgroundColor: '#1f1f1f',
        color: 'white',
        padding: '12px'
    },
    imgGit: {
        marginTop: theme.spacing(1),
        "&:hover": {
            "-webkit-transform":"scale(1.3)",
            transform: "scale(1.3)"
        }
    },
    imgEmail: {
        fontSize: "1.05rem",
        "&:hover": {
            "-webkit-transform":"scale(1.6)",
            transform: "scale(1.6)"
        } 
    },
    link: {
        textDecoration: 'none',
        '&:hover': {
            textDecoration: 'none'
          }
    },
    imgLinkedin: {
        marginTop: theme.spacing(1),
        "&:hover": {
            "-webkit-transform":"scale(1.3)",
            transform: "scale(1.3)"
        } 
    },
    footer: {
        overflowX: 'hidden',
        overflowY: 'hidden',
    },
    copyrightFooter: {
        padding: 1
    }
}));

function Copyright(){
    const classes = useStyles();
    
    return (
        <Typography className={classes.copyrightFooter} variant="body1" component="h2" align="center" gutterBottom>
            {'©'}
            {' '}
            {new Date().getFullYear()}
            {' '}
            <Link color="inherit" href="/">
                Jack Beryl
            </Link>
        </Typography>
    )
}

function Footer() {
    const classes = useStyles();
    return (
        <div className={classes.root}>
            <footer className={classes.footer}>
                <Divider />    
                <Grid container direction="row" justify="space-evenly">
                    <Grid item xs={6} sm={6} md={4} lg={4} xl={4}>  
                        <Typography variant="h5" align="center" gutterBottom>
                            <strong>Quick Links</strong>
                        </Typography>    
                        <Grid container direction="row" justify="center" alignItems="center">
                            <Grid item xs={12} sm={12} md={4} lg={3} xl={3}>
                                <Link className={classes.link} color="inherit" href="/Contact">
                                    <Typography variant="subtitle1" align="center" gutterBottom>
                                        {'Contact'}
                                    </Typography>  
                                </Link>  
                            </Grid>
                            <Grid item xs={12} sm={12} md={4} lg={3} xl={3}>
                                <Link className={classes.link} color="inherit" href="/About">
                                    <Typography variant="subtitle1" align="center" gutterBottom>
                                        {'About'}
                                    </Typography>  
                                </Link>                                                                                        
                            </Grid>
                        </Grid>                   
                    </Grid>
                    <Grid item xs={6} sm={6} md={4} lg={4} xl={4}>  
                        <Typography variant="h5" align="center" gutterBottom>
                            <strong>CONTACT</strong>
                        </Typography>    
                        <Grid container direction="row" justify="center" alignItems="center">
                            <Grid item xs={12} sm={12} md={4} lg={6} xl={4}>
                                <Link className={classes.link} color="inherit" href="mailto: contact@jack01.com">
                                    <Typography variant="subtitle1" align="center" gutterBottom>
                                        {'contact@jackb01.com'}
                                    </Typography>  
                                </Link>                                                                                        
                            </Grid>
                        </Grid>                    
                    </Grid>
                    <Grid item xs={6} sm={6} md={4} lg={4} xl={4}>  
                        <Typography variant="h5" align="center" gutterBottom>
                            <strong>Follow Me</strong>
                        </Typography>        
                        <Grid container direction="row" alignContent="center" justify="center" alignItems="center" className={classes.space}>
                            <Grid item xs={2} sm={2} md={1} lg={1} xl={1}>
                                <Link href="https://github.com/Jackb01" underline="none" color="inherit">
                                    <Typography align="center" gutterBottom>
                                        <GitHubIcon className={classes.imgGit} titleAccess="Github Account"/>     
                                    </Typography> 
                                </Link>                       
                            </Grid>
                            <Grid item xs={2} sm={2} md={1} lg={1} xl={1}>
                                <Link  href="https://linkedin.com/in/jack-beryl/" underline="none" color="inherit">
                                    <Typography align="center" gutterBottom>
                                        <LinkedInIcon className={classes.imgLinkedin} titleAccess="Linkedin Account"/>                           
                                    </Typography>  
                                </Link>
                            </Grid>                                      
                        </Grid>                
                    </Grid>
                    <Grid item xs={6} sm={6} md={4} lg={4} xl={4}>  
                        <Copyright />  
                    </Grid>
                </Grid>                
            </footer>
        </div>
    )
}

export default Footer
