import React from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import SecurePay from '../../../resources/images/Secure_payment.png';
//import ContactMe from '../../../resources/images/contact-us.svg';
import OnlinePay from '../../../resources/images/Online_pay.png';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
    MyQR: {
        [theme.breakpoints.down("xs")]: {    
            height: '30vmin',
            width: '30vmin', 
        },
        [theme.breakpoints.up("sm")]: {
            height: '35vmin',
            width: '35vmin'
        },
        [theme.breakpoints.up("md")]: {
            height: '20vmin',
            width: '20vmin',
        },
        [theme.breakpoints.up("lg")]: {
            height: '25vmin',
            width: '25vmin',       
        },
        [theme.breakpoints.up("xl")]: {
            height: '25vmin',
            width: '25vmin',
        },
    },
    SQP: {
        [theme.breakpoints.down("xs")]: {    
            height: '22vmin',
            width: '30vmin', 
        },
        [theme.breakpoints.up("sm")]: {
            height: '22vmin',
            width: '30vmin'
        },
        [theme.breakpoints.between('750', '1000')]: {
            height: '12vmin',
            width: '15vmin',
        },
        [theme.breakpoints.up("md")]: {
            height: '12vmin',
            width: '16vmin',
        },
        [theme.breakpoints.up("lg")]: {
            height: '10vmin',
            width: '13vmin',       
        },
        [theme.breakpoints.up("xl")]: {
            height: '10vmin',
            width: '13vmin',
        },
    },
    onPayment: {
        [theme.breakpoints.down("xs")]: {    
            height: '48vmin',
            width: '48vmin', 
        },
        [theme.breakpoints.up("sm")]: {
            height: '48vmin',
            width: '48vmin'
        },
        [theme.breakpoints.between('750', '1032')]: {
            height: '32vmin',
            width: '32vmin',
        },
        [theme.breakpoints.up("md")]: {
            height: '28vmin',
            width: '30vmin',
        },
        [theme.breakpoints.up("lg")]: {
            height: '28vmin',
            width: '28vmin',       
        },
        [theme.breakpoints.up("xl")]: {
            height: '28vmin',
            width: '28vmin',
        },
    },
    title: {
        textAlign: 'center',
    },
    titleText: {
        
    }
}));

function DialogQR(props) {
    const classes = useStyles();       
    return (
        <div>
            <Dialog
                open={props.openDialog}
                onClose={props.closeDialog}
                aria-labelledby="responsive-dialog-title">

                <DialogTitle id="responsive-dialog-title" className={classes.title}>
                    <Grid spacing={6} container direction="row" justify="flex-start" alignItems="center">
                        <Grid item spacing={4} xs={4} sm={4} md={4} lg={3} xl={3}>
                            <img src={SecurePay} className={classes.SQP} alt="Secure Pay" />
                        </Grid>
                        <Grid item spacing={8} xs={8} sm={8} md={8} lg={9} xl={9}>
                            <strong style={{fontSize: '25px',}}>
                                Payment Methods
                            </strong>
                        </Grid>
                    </Grid>
                    
                </DialogTitle>
                <DialogContent dividers>
                    <DialogContentText>
                        <Grid container spacing={2} direction="row" justify="center" alignItems="center">
                            <Grid item xs={8} sm={7} md={7} lg={7} xl={7} style={{marginBottom: '10px'}}>
                                <img src={OnlinePay} className={classes.onPayment} alt="Online Payment" />
                            </Grid>
                            <Grid spacing={2} item xs={6} sm={5} md={5} lg={5} xl={5}>
                                <form action="https://www.paypal.com/cgi-bin/webscr" method="post" target="_top">
                                    <input type="hidden" name="cmd" value="_donations" />
                                    <input type="hidden" name="business" value="8DBSWL7L8TARJ" />
                                    <input type="hidden" name="item_name" value="Online ( Course, Project ) or Donation" />
                                    <input type="hidden" name="currency_code" value="USD" />
                                    <input type="image" src="https://www.paypalobjects.com/en_US/i/btn/btn_donateCC_LG.gif" 
                                                        border="0" name="submit" title="PayPal - The safer, easier way to pay online!" 
                                                        alt="Donate with PayPal button" />                                    
                                    <img alt="" border="0" src="https://www.paypal.com/en_US/i/scr/pixel.gif" width="1" height="1" />
                                </form>
                            </Grid>
                        </Grid> 
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={props.closeDialog} color="primary">
                        Close
                    </Button>
                </DialogActions>
            </Dialog> 
        </div>          
    )
}

export default DialogQR;
  