import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { createMuiTheme, responsiveFontSizes, ThemeProvider } from '@material-ui/core/styles';
import Link from '@material-ui/core/Link';


let theme = createMuiTheme();
theme = responsiveFontSizes(theme);

const useStyles = makeStyles(theme => ({
    typoName: {
        color: 'white',
        [theme.breakpoints.down("xs")]: {    
            marginTop: 85,//85
            marginLeft: 2    
        },
      [theme.breakpoints.up("sm")]: {
            marginTop: 90, //90
        },
      [theme.breakpoints.up("md")]: {
            marginTop: 100,
        },
      [theme.breakpoints.up("lg")]: {
            marginTop: 150,        
        },
      [theme.breakpoints.up("xl")]: {
            marginTop: 200,
      },
    },
    typoHelp: {
      color: 'white',
      border: '5px solid white',
      backgroundColor: 'transparent',
      textAlign: 'center',
      [theme.breakpoints.down("xs")]: {    
            marginTop: 15,    
        },
      [theme.breakpoints.up("sm")]: {
            marginTop: 30,
        },
      [theme.breakpoints.up("md")]: {
            marginTop: 70,
        },
      [theme.breakpoints.up("lg")]: {
            marginTop: 80,    
            "&:hover:after": {
                content: "'\\003e'",
                position: 'relative',
                left: '10%',
                verticalAlign: 'middle'
              },    
        },
      [theme.breakpoints.up("xl")]: {
            marginTop: 80,
            "&:hover:after": {
                content: "'\\003e'",
                position: 'relative',
                left: '10%',
                verticalAlign: 'middle'
              },
      }, 
      "&:hover": {
        border: '8px solid transparent',
        backgroundColor: '#00d8ff',
        transition: '.8s',
      }, 
    },
    firstTitle: {
        color: "#00d8ff"
    }
}));

export default function HeaderPresentation() {
    const classes = useStyles();

    return (
        <div>
            <Grid container direction="row" justify="center" alignItems="center" className={classes.typoName} >      
                <Grid item xs={12} sm={10} md={8} lg={6} xl={4}>
                    <ThemeProvider theme={theme}>
                        <Typography className={classes.firstTitle} variant="h1">
                            Hello,  
                        </Typography>
                        <Typography variant="h1">
                            I'm Jack Beryl
                        </Typography>
                        <Typography style={{marginLeft: '7px'}} variant="h6">
                            I am a software Developer
                        </Typography>
                    </ThemeProvider>                                       
                </Grid> 
            </Grid>
            <Grid container direction="row" justify="center" alignItems="center">
                <Grid item xs={11} sm={9} md={7} lg={5} xl={3}>
                    <ThemeProvider theme={theme}>
                        <Link href="/#Services" underline="none" color="textPrimary">
                            <Typography variant="h4" className={classes.typoHelp} gutterBottom>
                                Discover How I Can Help You
                            </Typography>
                        </Link>                      
                    </ThemeProvider>                   
                </Grid>
            </Grid>
        </div>
    )
}
