import React from 'react';
import { makeStyles, createMuiTheme, responsiveFontSizes, ThemeProvider } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import AndroidCard from '../../../resources/images/Android_Card.jpg';
import SoftCard from '../../../resources/images/Soft_Card.jpeg';
import NetCard from '../../../resources/images/Net_Card.png';
import Paper from '@material-ui/core/Paper';
import DialogM from '../Dialog/Dialog';

let theme = createMuiTheme();
theme = responsiveFontSizes(theme);

const useStyles = makeStyles(theme => ({
    paper: {
        marginBottom: 10,
        flexGrow: 1,
        overflowX: 'hidden',
        overflowY: 'hidden',
        [theme.breakpoints.down("xs")]: {    
            marginTop: 10,    
        },
        [theme.breakpoints.up("sm")]: {
            marginTop: 15,
        },
        [theme.breakpoints.up("md")]: {    
            marginTop: 15,    
        },
        [theme.breakpoints.up("lg")]: {
            marginTop: 30,
        },
        [theme.breakpoints.up("xl")]: {    
            marginTop: 50,    
        },
    },
    root: {
        maxWidth: 350,
        [theme.breakpoints.down("xs")]: {    
            maxWidth: 350,    
        },
        [theme.breakpoints.up("sm")]: {
            maxWidth: 320,
        },
        [theme.breakpoints.up("md")]: {
            maxWidth: 360,
        },
        [theme.breakpoints.up("lg")]: {
            maxWidth: 380,        
        },
        [theme.breakpoints.up("xl")]: {
            maxWidth: 480,
        },
    },
    service: {
        fontSize: "55px",
        [theme.breakpoints.down("xs")]: {    
            marginBottom: 40,
            marginTop: 55,    
        },
        [theme.breakpoints.up("sm")]: {
            marginBottom: 40,
            marginTop: 55,
        },
        [theme.breakpoints.up("md")]: {
            marginBottom: 50,
            marginTop: 60,
        },
        [theme.breakpoints.up("lg")]: {
            marginBottom: 65,
            marginTop: 65,       
        },
        [theme.breakpoints.up("xl")]: {
            marginBottom: 65,
            marginTop: 65,
        },
    },
    media: {
        paddingTop: '56.25%'
    },
    card: {
        height: '100%',
        borderBottom: "1px solid #b1b1b1",
        display: 'flex',
        flexDirection: 'column'
    },
    cardContent: {
        flexGrow: 1
    },
    linkToMore: {
        color: "#00bee0"
    },
}));



export default function Main() {

    const classes = useStyles();

    const [open, setOpen] = React.useState(false);
    const [open1, setOpen1] = React.useState(false);
    const [open2, setOpen2] = React.useState(false);

    const subjectCards = [
        {
            id: 0,
            image: SoftCard,
            imageTitle: 'Software Development',
            title: 'Software Development',
            content: ' Software development is the process of conceiving, specifying, designing, programming'+ 
                     ' documentating, testing and bug fixing involved in creating and maintaining applications.',
            titleModal: 'Software Developer',
            contentModel: "I'm a Software Developer since few years now, I love building application in Java (Desktop, Web & Android)."+
                          "\nThe Recent Projects that have help me to gain more skills in Software fields are: "+
                          "\n* E-Voting System (Client-Server Desktop App) done in Java Swing"+
                          "\n* Suspension Management of Carriers of Petroleum Product (Web App) done in Java EE"+
                          "\n* Challengemee Social Network done in Java EE and React Js using REST API"
        },
        {
            id: 1,
            image: AndroidCard,
            imageTitle: 'Android Development',
            title: 'Android Development',
            content: ' Android software development is the process of conceiving, designing, programming & testing mobile'+ 
                     ' application which are created for devices running on the Android Operating System.',
            titleModal: 'Android Developer',
            contentModel: "I'm a Software Developer since few years now, I love building application in Java (Desktop, Web & Android)."+
                          "\nThe Recent Projects that have help me to gain more skills in Android development are: "+
                          "\n* Google Map Localisation"+
                          "\n* Contact Application"
        },
        {
            id: 2,
            image: NetCard,
            imageTitle: 'Computer Network',
            title: 'Computer Network',
            content: "Computer Network is the concept of interconnecting many nodes in other to share resources,"+ 
                     ' The data transmission between does nodes is supported over data links',
            titleModal: 'Network Engineer',
            contentModel: "I'm an Associate Network Engineer since few months, I have passed the Cisco Routing and Switching Certification"+
                          " More efficient on (Dynamic Routing, Inter VLAN Routing & ACL)."+
                          "\nMy Love for network has grown because of data security, the methods of protecting packets transmitted."+
                          "\nI haven't yet work on a Network Project hence no Professional Experience but I'm sure to find one soon :)"
        }
    ];

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleClickOpen1 = () => {
        setOpen1(true);
    };

    const handleClose1 = () => {
        setOpen1(false);
    };

    const handleClickOpen2 = () => {
        setOpen2(true);
    };

    const handleClose2 = () => {
        setOpen2(false);
    };

  return (
    <div>
        <Paper className={classes.paper} elevation={0}>
            <Grid id="Services" container direction="row" justify="center" alignItems="center">
                <Grid item xs={7} sm={3} md={3} lg={3} xl={1}>
                    <ThemeProvider theme={theme}>
                        <Typography className={classes.service} variant="h3" align="center">
                            <strong> Services </strong> 
                        </Typography>
                    </ThemeProvider>                                       
                </Grid>
            </Grid>
            <Grid container spacing={2} direction="row" justify="space-evenly" alignItems="center">
                <Grid item xs={12} sm={12} md={4} lg={4} xl={4} className={classes.root}>                          
                    <Card className={classes.card}>
                        <CardActionArea>
                            <CardMedia
                                className={classes.media}
                                image= {subjectCards[0].image}
                                title= {subjectCards[0].imageTitle} />
                            <CardContent className={classes.cardContent}>
                                <Typography gutterBottom variant="h5" component="h2">
                                    {subjectCards[0].title}
                                </Typography>
                                <Typography variant="body2" color="textSecondary" component="p">
                                    {subjectCards[0].content}
                                </Typography>
                            </CardContent>
                        </CardActionArea>
                        <CardActions>
                            <Button className={classes.linkToMore} size="small" onClick={handleClickOpen}>                                                                             
                                Learn More
                            </Button>
                        </CardActions>
                    </Card>                                           
                </Grid>
                <Grid item xs={12} sm={12} md={4} lg={4} xl={4} className={classes.root}>                          
                    <Card className={classes.card}>
                        <CardActionArea>
                            <CardMedia
                                className={classes.media}
                                image= {subjectCards[1].image}
                                title= {subjectCards[1].imageTitle} />
                            <CardContent className={classes.cardContent}>
                                <Typography gutterBottom variant="h5" component="h2">
                                    {subjectCards[1].title}
                                </Typography>
                                <Typography variant="body2" color="textSecondary" component="p">
                                    {subjectCards[1].content}
                                </Typography>
                            </CardContent>
                        </CardActionArea>
                        <CardActions>
                            <Button className={classes.linkToMore} size="small" onClick={handleClickOpen1}>                                                                             
                                Learn More
                            </Button>
                        </CardActions>
                    </Card>                                           
                </Grid>          
                <Grid item xs={12} sm={12} md={4} lg={4} xl={4} className={classes.root}>                          
                    <Card className={classes.card}>
                        <CardActionArea>
                            <CardMedia
                                className={classes.media}
                                image= {subjectCards[2].image}
                                title= {subjectCards[2].imageTitle} />
                            <CardContent className={classes.cardContent}>
                                <Typography gutterBottom variant="h5" component="h2">
                                    {subjectCards[2].title}
                                </Typography>
                                <Typography variant="body2" color="textSecondary" component="p">
                                    {subjectCards[2].content}
                                </Typography>
                            </CardContent>
                        </CardActionArea>
                        <CardActions>
                            <Button className={classes.linkToMore} size="small" onClick={handleClickOpen2}>                                                                             
                                Learn More
                            </Button>
                        </CardActions>
                    </Card>                                           
                </Grid>               
            </Grid>
            <DialogM openDialog={open} closeDialog={handleClose} title={subjectCards[0].titleModal} content={subjectCards[0].contentModel}/>                                                                 
            <DialogM openDialog={open1} closeDialog={handleClose1} title={subjectCards[1].titleModal} content={subjectCards[1].contentModel}/>                                                   
            <DialogM openDialog={open2} closeDialog={handleClose2} title={subjectCards[2].titleModal} content={subjectCards[2].contentModel}/>                                                             
        </Paper>
    </div>   
    );
} 