import React from "react";
import { Button, MenuItem } from "@material-ui/core";
import { withStyles } from "@material-ui/core/styles";
import BtnCollapse from "./BtnCollapse";
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';


const styles = theme => ({
  root: {
    position: "absolute",
    right: 0
  },
  buttonBar: {
    [theme.breakpoints.down("xs")]: {
      display: "none"
    },
    margin: "10px",
    paddingLeft: "16px",
    right: 0,
    position: "relative",
    width: "100%",
    background: "transparent"
  },
  linkStyle: {
      '&:hover': {
        backgroundColor: 'rgba(0,0,0,0)'
      },
      '&:after': {
        bottom: 0,
        content: "''",
        display: 'block',
        height: 2,
        left: 0,
        position: 'absolute',
        backgroundColor: '#00d8ff',
        transition: 'width 0.5s ease 0s, left 0.5s ease 0s',
        width: 0,
      },
      '&:hover:after': {
            width:' 100%', 
            left: 0,
      }
  },
  appBarLink: {
      fontWeight: 420
  }
});

const AppBarCollapse = props => (

  <div className={props.classes.root}>
      
    <BtnCollapse>
        <Link href='/' underline="none" color="textPrimary">
            <MenuItem>            
                Home
            </MenuItem>
        </Link>
        <Link href='/#Services' underline="none" color="textPrimary">
            <MenuItem>
                Services
            </MenuItem>
        </Link>
        <Link href='/About' underline="none" color="textPrimary">
            <MenuItem>
                About
            </MenuItem>
        </Link>
        <Link href="/Contact" underline="none" color="textPrimary">
            <MenuItem>
                Contact
            </MenuItem>
        </Link>                 
    </BtnCollapse>

    <div className={props.classes.buttonBar} id="appbar-collapse">
    
        <Link  href='/' underline="none" color="textPrimary">
            <Button className={props.classes.linkStyle} color="inherit">         
                <Typography className={props.classes.appBarLink} variant="body1"> 
                    Home
                </Typography>
            </Button>
        </Link>
        <Link href='/#Services' underline="none" color="textPrimary">
            <Button className={props.classes.linkStyle} color="inherit">           
                <Typography className={props.classes.appBarLink} variant="body1"> 
                    Services
                </Typography>
            </Button>
        </Link>
        <Link href="/About" underline="none" color="textPrimary">
            <Button className={props.classes.linkStyle} color="inherit">          
                <Typography className={props.classes.appBarLink} variant="body1"> 
                    About
                </Typography>
            </Button>
        </Link>
        <Link href="/Contact" underline="none" color="textPrimary">
            <Button className={props.classes.linkStyle} color="inherit">
                <Typography className={props.classes.appBarLink} variant="body1"> 
                    Contact
                </Typography>
            </Button>
        </Link>       
    </div>
  </div>
);

export default withStyles(styles)(AppBarCollapse);
