import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import NavBar from './NavBar';
import Paper from '@material-ui/core/Paper';
import backgroundImageM from '../../../resources/images/home-header-dark.jpg';
import HeaderPresentation from '../Presentation/HeaderPresentation';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    overflowX: 'hidden',
    display: 'flex',
  },
  large: {
    width: theme.spacing(7),
    height: theme.spacing(7),
  },
  paperContainer: {
    height: 800,
    width: '100%',
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center center',
    [theme.breakpoints.down("xs")]: {
      height: 400,
      width: '100%',
      backgroundSize: 'cover',
      backgroundRepeat: 'no-repeat',
      backgroundImage: `url(${backgroundImageM})`
    },
    [theme.breakpoints.up("sm")]: {
      height: 400,
      width: '100%',
      backgroundSize: 'cover',
      backgroundRepeat: 'no-repeat',
      backgroundPosition: 'center center',
      backgroundImage: `url(${backgroundImageM})`
    },
    [theme.breakpoints.up("md")]: {
      height: 600,
      width: '100%',
      backgroundSize: 'cover',
      backgroundRepeat: 'no-repeat',
      backgroundPosition: 'center center',
      backgroundImage: `url(${backgroundImageM})`
    },
    [theme.breakpoints.up("lg")]: {
      height: 700,
      width: '100%',
      backgroundSize: 'cover',
      backgroundRepeat: 'no-repeat',
      backgroundPosition: 'center center',
      backgroundImage: `url(${backgroundImageM})`
    },
    [theme.breakpoints.up("xl")]: {
      height: 900,
      width: '100%',
      backgroundSize: 'cover',
      backgroundRepeat: 'no-repeat',
      backgroundPosition: 'center center',
      backgroundImage: `url(${backgroundImageM})`
    },  
    backgroundImage: `url(${backgroundImageM})`
  },
  appBarBottom: {
      borderBottom: "1px solid #00d8ff"
  },
}));


export default function NAppBar() {
  const classes = useStyles();

  return (
      <div className={classes.root}>   
        <Paper className={classes.paperContainer}>    
          <NavBar />
          <HeaderPresentation />
        </Paper>
      </div>   
  );
}
